@import "colors";

.home {
  height: 100vh;
  position: relative;
  //doing this because framer motions throws elements out while in rizz
  overflow-x: hidden;

  > div {
    padding-left: 8rem;
    padding-top: 15rem;

    > h1 {
      font-size: 4rem;
      text-transform: uppercase;
    }

    > p {
      font-size: 1.2rem;
    }

    > a {
      display: block;
      margin: 2rem 0;
      width: 10rem;
      background-color: $red;
      color: white;
      text-align: center;
      border: none;
      font-size: 1rem;
      border-radius: 10px;
      padding: 1rem;
      cursor: pointer;

      &:hover {
        background-color: $darkRed;
      }
    }
  }
  &::before {
    content: "";
    width: 100%;
    height: 100%;
    background-image: url("../assets/bg.svg");
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    left: 0%;
    top: 0%;
    z-index: -1;
  }
}

@media screen and (max-width: 600px) {
  .home {
    > div {
      text-align: center;
      padding-top: 10rem;
      padding-left: 0rem;

      > a {
        margin: 2rem auto;
      }
    }

    &::before {
      background-position-x: -410px;
    }
  }
}
